@import "sm";

/*-----------------------------------------
	Theme
-----------------------------------------*/
html, body {
	height: 100%;
}
body {
	background: #343268;
	background: -moz-linear-gradient(left, #343268 0%, #27264f 100%);
	background: -webkit-linear-gradient(left, #343268 0%,#27264f 100%);
	background: linear-gradient(to right, #343268 0%,#27264f 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#343268', endColorstr='#27264f',GradientType=1 );
	font-family: 'Open Sans', sans-serif;
}
button, input[type="submit"], a {
	cursor: pointer;
}
.btn-violet {
    background-color: #343268;
    border-color: #464479;
    color: #fff;
    &:focus {
	    background-color: #5c5cfb;
	    border-color: #80bdff;
	    outline: 0;
	    box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25); 	
    }
}
.btn-grey {
	background-color: #e2e5ee;
	border-color: #e2e5ee;
	color: #9098b1;
}
.btn--modal-p {
	padding: 8px 30px;
}

/*-----------------------------------------
	Left
-----------------------------------------*/
.left {
	background-color: #ffffff;
	border-right: 2px solid #ebebeb;
}

/* Profile info */
.profile {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 15px 0;
	font-size: 14px;
}
.profile__box {
	margin: 0 0 5px 0;
	padding: 4px;
	border: 4px solid #f8f9fa;
	border-radius: 100%;
}
.profile__photo {
	border-radius: 100%;
}
.profile__info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;	
}
.profile__name {
	margin: 0 0 3px 0;
	font-weight: 600;
}
.profile__balance {
	margin: 0;
}

/* Search form */
.search-contacts {
	margin: 0 0 15px 0;
}

/* User contacts */
.contacts__list {
	flex-direction: column;
	flex-wrap: initial;
	width: calc(100% + 30px);
	height: calc(100vh - 277px);
	overflow-y: auto;
}
.contacts__chat-link {
	padding: 7px 15px;
	color: #222222;
	&:hover {
		background-color: #f8f9fa;
		color: #5c5cfb;
		text-decoration: none;
	}
}
.contacts__chat-link--active {
	background-color: #515793;
	color: #ffffff;
	&:hover {
		background-color: #515793;
		color: #ffffff;
	}
}
.contact__photo {
	border-radius: 100%;
}
.contact__name {
	font-size: 13px;
	font-weight: 600;
}

/* Panel tab */
.panel {
	border-top: 2px solid #ebebeb;
	& > .row {
		height: 100%;
	}
}
.panel__btn {
	position: relative;
	width: 100%;
	height: 100%;
	padding: 12px 0 13px 0;
	flex-direction: column;
	background-color: transparent;
	border: none;
	& .badge {
		position: absolute;
		top: 7px;
		right: 7px;
	}
	& .panel__caption {
		color: #222222;
	}
	&:hover i {
		color: #5c5cfb;
	}
	& i {
		color: #27264F;
	}
}
.panel__btn--active {
	& i {
		color: #5c5cfb;
	}
}
.panel__caption {
	padding-top: 4px;
	font-size: 14px;
}

/* Txs */
.txs {
	padding-top: 10px;
	padding-bottom: 10px;
	overflow-y: auto;
}
.txs__item {
	display: flex;
	align-items: center;
	width: 100%;
	padding: 10px 15px;
	&:hover {
		background-color: #f8f9fa;
	}
}
.txs__circle {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 40px;
	height: 40px;
	margin: 0 15px 0 0;
	border-radius: 100%;
	background-color: #515793;
	color: #ffffff;
}
.txs__right {
	display: flex;
	flex-direction: column;
}
.txs__from,.txs__eos {
	margin: 0;
	font-size: 14px;
	color: #6e6f89;
}

/*-----------------------------------------
	Right
-----------------------------------------*/
.right {
	padding-top: 15px;
	padding-bottom: 15px;
	background-color: #f3f3f8;
}

/* User messages */
.messages {
	height: calc(100vh - 68px);
	overflow-y: auto;
}
.messages__box {
	padding: 0 6px;
}
.messages__item {
	position: relative;
	margin: 0 0 15px 0;
	padding: 7px 11px 8px 11px;
	background-color: #5c5cfb;
	border-radius: 5px;
	box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
	font-size: 14px;
	color: #f4f4ff;
	&::before {
		content: '';
		position: absolute;
		top: 15px;
		right: -6px;
		display: block;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 3.5px 0 3.5px 6px;
		border-color: transparent transparent transparent #5c5cfb;	
	}
}
.messages__item--me {
	background-color: #ffffff;
	color: #222222;
	&::before {
		left: -6px;
		border-style: solid;
		border-width: 3.5px 6px 3.5px 0;
		border-color: transparent #ffffff transparent transparent;			
	}
}

@media (max-width: 991px) {
	.profile {
		flex-direction: row;
		justify-content: flex-start;
	}
	.profile__back {
		width: 35px;
		height: 35px;
		margin: 0 10px 0 0;
		padding: 0;
		background-color: transparent;
		border: none;
		&:focus {
			outline: none;
		}
		& .fa {
			font-size: 24px;
			color: #27264f;
			&:hover {
				color: #5c5cfb;
			}
		}
	}
	.profile__info {
		align-items: flex-start;
	}
	.profile__box {
		margin: 0 15px 0 0;
	}
	.profile__photo {
		max-width: 40px;
	}
	.contacts__list {
		height: calc(100vh - 207px);
	}	
	.panel {
		order: 2;
	}
	.menu-item {
	    -ms-flex: 0 0 25%;
	    flex: 0 0 25%;
	    max-width: 25%;		
	}
	.left--txs {
		height: calc(100vh - 68px);
	}
	.messages {
		height: calc(100vh - 222px);
	}
	.messages__item {
		width: 93%;
		margin-left: auto;
	}
	.messages__item--me {
		margin-left: initial;
	}
	.modal-body {
		& .col-md-6 {
			-ms-flex: 0 0 50%;
			flex: 0 0 50%;
			max-width: 50%;
			padding-left: 10px;
			padding-right: 10px;
		}
	}
}